import React, { useMemo } from 'react';
import classNames from 'classnames';
import { useDebounce } from 'use-debounce';
import { Container, Grid, Input, Label } from 'semantic-ui-react';

import withAuth from './with-auth';
import useField from './use-field';

import DATABASE from './database.json';

import styles from './App.module.css';

import 'semantic-ui-css/semantic.min.css';

const getAssociations = (database1, word1, database2, word2) => {
  let associations = [];

  if (word1) {
    const word1Associations = database1[word1];

    if (word2) {
      const word2Associations = database2[word2];

      if (word1Associations && word2Associations) {
        Object.entries(word1Associations).forEach(([word, weight]) => {
          if (word in word2Associations) {
            associations.push([word, (weight + word2Associations[word]) / 2]);
          }
        });
      }
    } else if (word1Associations) {
      associations = Object.entries(word1Associations);
    }

    associations.sort((association1, association2) => {
      if (association1[1] > association2[1]) {
        return -1;
      }

      if (association1[1] < association2[1]) {
        return 1;
      }

      return 0;
    });
  }

  return associations;
};

const App = () => {
  const [word1, handleWord1Change] = useField('');
  const [word2, handleWord2Change] = useField('');

  const [word1Debounced] = useDebounce(word1, 500);
  const [word2Debounced] = useDebounce(word2, 500);

  const word1Clean = word1Debounced.trim().toLowerCase().replace('ё', 'е');
  const word2Clean = word2Debounced.trim().toLowerCase().replace('ё', 'е');

  const {
    associationsCustom,
    associationsSociation,
    associationsCustomSociation,
    associationsSociationCustom,
  } = useMemo(() => {
    const associationsCustom = getAssociations(
      DATABASE.custom,
      word1Clean,
      DATABASE.custom,
      word2Clean,
    );

    const associationsSociation = getAssociations(
      DATABASE.sociation,
      word1Clean,
      DATABASE.sociation,
      word2Clean,
    );

    let associationsCustomSociation = [];
    let associationsSociationCustom = [];

    if (word2Clean) {
      associationsCustomSociation = getAssociations(
        DATABASE.custom,
        word1Clean,
        DATABASE.sociation,
        word2Clean,
      );

      associationsSociationCustom = getAssociations(
        DATABASE.sociation,
        word1Clean,
        DATABASE.custom,
        word2Clean,
      );
    }

    return {
      associationsCustom,
      associationsSociation,
      associationsCustomSociation,
      associationsSociationCustom,
    };
  }, [word1Clean, word2Clean]);

  return (
    <>
      <div className={styles.header}>
        <Container>
          <Grid padded>
            <Grid.Row columns={2}>
              <Grid.Column className={styles.column}>
                <Input
                  autoFocus
                  fluid
                  value={word1}
                  placeholder="Слово"
                  size="big"
                  onChange={handleWord1Change}
                />
              </Grid.Column>
              <Grid.Column>
                <Input
                  fluid
                  value={word2}
                  placeholder="Вместе со словом"
                  size="big"
                  onChange={handleWord2Change}
                />
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Container>
      </div>
      <div className={styles.content}>
        <Container>
          <Grid padded>
            {associationsCustom.length > 0 && (
              <Grid.Row>
                <div className={styles.words}>
                  {associationsCustom.map((association, i) => (
                    <Label
                      horizontal
                      key={association[0]}
                      className={classNames(styles.word, i % 2 || styles.wordRed)}
                    >
                      {association[0]}
                    </Label>
                  ))}
                </div>
              </Grid.Row>
            )}
            {associationsSociation.length > 0 && (
              <Grid.Row>
                <div className={styles.words}>
                  {associationsSociation.map((association, i) => (
                    <Label
                      horizontal
                      key={association[0]}
                      className={classNames(styles.word, i % 2 || styles.wordBlue)}
                    >
                      {association[0]}
                    </Label>
                  ))}
                </div>
              </Grid.Row>
            )}
            {associationsCustomSociation.length > 0 && (
              <Grid.Row>
                <div className={styles.words}>
                  {associationsCustomSociation.map((association, i) => (
                    <Label
                      horizontal
                      key={association[0]}
                      className={classNames(styles.word, i % 2 || styles.wordViolet)}
                    >
                      {association[0]}
                    </Label>
                  ))}
                </div>
              </Grid.Row>
            )}
            {associationsSociationCustom.length > 0 && (
              <Grid.Row>
                <div className={styles.words}>
                  {associationsSociationCustom.map((association, i) => (
                    <Label
                      horizontal
                      key={association[0]}
                      className={classNames(styles.word, i % 2 || styles.wordViolet)}
                    >
                      {association[0]}
                    </Label>
                  ))}
                </div>
              </Grid.Row>
            )}
          </Grid>
        </Container>
      </div>
    </>
  );
};

export default withAuth(App);
